import React, { useState, useEffect } from "react";
import { Trans, useTranslation } from "gatsby-plugin-react-i18next";
import { FiMenu, FiX } from "react-icons/fi";
import { FaArrowUp } from "react-icons/fa";
import { auth, db } from "../firebaseConfig";
import { getDoc, doc } from "firebase/firestore";
import { Link } from "gatsby";
import { useLocation } from "@reach/router";

interface NavProps {
  data?: {
    menu: string[];
  };
}

const Navigation: React.FC<NavProps> = ({ data }) => {
  const { t } = useTranslation();
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [showBackToTop, setShowBackToTop] = useState(false);
  const [isAdmin, setIsAdmin] = useState(false);
  const location = useLocation();

  if (!data || !data.menu) {
    return null;
  }

  useEffect(() => {
    const checkAdmin = async () => {
      const user = auth.currentUser;
      if (user) {
        const userDoc = await getDoc(doc(db, "users", user.uid));
        if (userDoc.exists() && userDoc.data().role === "admin") {
          setIsAdmin(true);
        }
      }
    };
    checkAdmin();
  }, []);

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const scrollToTop = () => {
    window.scrollTo({ top: 0, behavior: "smooth" });
  };

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 100) {
        setShowBackToTop(true);
      } else {
        setShowBackToTop(false);
      }
    };
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);
  const isHome = location.pathname === "/" || location.pathname === "/en/" || location.pathname === "/es/";
  return (
    <nav className={styles.container}>
      <button onClick={toggleMenu} className={styles.burgerButton}>
        {isMenuOpen ? <FiX className="text-white fixed right-4 z-[70] top-4" size={36} /> : <FiMenu className="text-white" size={36} />}
      </button>

      <ul className={`${styles.buttonsWrapper} ${isMenuOpen ? styles.menuOpen : styles.menuClosed}`}>
        {isHome && data.menu.map((item: string, index: number) => (
          <li key={index} className={styles.buttonsContainer}>
            <button
              onClick={() => {
                document.getElementById(item)?.scrollIntoView({ behavior: "smooth" });
                setIsMenuOpen(!isMenuOpen)
              }}
              className={styles.buttons}>
              <Trans i18nKey={`menu.${item}`}>
                {t(`menu.${item}`)}
              </Trans>
            </button>
          </li>
        ))}
        {isHome && <li className={styles.access}>
          <a href="/login" className="mr-4 self-center text-sm text-nowrap">{t("login")}</a>
          <a href="/register" className={styles.signupButton}>{t("signup")}</a>
        </li>}
        {isAdmin && <li className={styles.access}>
          <Link to="/admin">{t("adminPanel")}</Link>
        </li>}
      </ul>


      {showBackToTop && (
        <button 
        onClick={scrollToTop} 
        className={styles.backToTopButton}
        aria-label="Back to top">
          <FaArrowUp size={20} />
        </button>
      )}
    </nav>
  );
};

export default Navigation;

const styles = {
  container: "flex justify-between self-center px-0 sm:px-4 py-2 mx-0 sm:mx-8 rounded-none sm:rounded-full relative z-20",
  burgerButton: "md:hidden block focus:outline-none mx-auto",
  buttonsWrapper: "flex flex-col md:flex-row justify-evenly items-center w-full fixed sm:relative left-0 top-0 py-8 sm:py-0 gap-4 bg-secondary sm:bg-transparent",
  buttonsContainer: "my-4 sm:my-0",
  buttons: "text-terciary text-xl px-4 py-0 hover:underline hover:text-light text-nowrap",
  menuOpen: "block md:flex",
  menuClosed: "hidden md:flex",
  backToTopButton: "fixed bottom-4 right-4 bg-secondary text-white p-2 rounded-full shadow-lg z-50",
  access: "flex text-light self-center ml-0 sm:ml-8 text-sm",
  signupButton: "bg-dark sm:bg-secondary hover:bg-semiLight text-light text-sm hover:text-secondary px-6 py-2 rounded-xl text-nowrap",
};
