import React from "react";

import { AiFillTikTok } from "react-icons/ai";
import {
  FaSquareFacebook,
  FaSquarePhone,
  FaSquareWhatsapp,
} from "react-icons/fa6";
import { BiLogoInstagramAlt } from "react-icons/bi";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { Company } from "../interfaces";
import MainLogo from "./mainLogo";
import images from "../images";

interface Props {
  data: Company;
}

const currentYear = new Date().getFullYear();

function formatPhoneNumber(phoneNumber: string) {
  const cleanedPhoneNumber = phoneNumber.replace(/\D+/g, "");
  if (cleanedPhoneNumber.startsWith("52")) {
    return `+52 ${cleanedPhoneNumber.slice(2, 5)} ${cleanedPhoneNumber.slice(
      5,
      8
    )} ${cleanedPhoneNumber.slice(8, 12)}`;
  } else {
    return cleanedPhoneNumber;
  }
}

const Footer: React.FC<Props> = ({ data }) => {
  return (
    <>
      <footer className="flex flex-col sm:flex-row bg-dark p-2 sm:p-0 w-full">
        <div className="flex w-full sm:w-1/3 px-32 py-8">
          <img src={images.logo_white.src} alt={images.logo_white.alt} />
        </div>
        <div className="flex flex-col w-full sm:w-1/3 text-secondary text-center self-center">
          <div className="w-full">
            {data.city}, {data.province}, {data.country}
          </div>
          <div className="w-full">
            <a
              href={`tel:${data.phone}`}
              className="flex flex-row gap-2 justify-center items-center text-lg font-bold"
            >
              <FaSquarePhone />
              {formatPhoneNumber(data.phone)}
            </a>
          </div>
          <div className="w-full">
            &copy; {currentYear} {data.companyName}
          </div>
        </div>
        <div className="flex flex-row w-24 sm:w-1/3 text-secondary self-center justify-center -order-2 sm:order-1">
          <a href={data.instagram} className={styles.social} target="_blank">
            <BiLogoInstagramAlt className="w-8 sm:w-12 h-8 sm:h-12" />
            <span className="text-xs">Instagram</span>
          </a>
          <a href={data.facebook} className={styles.social} target="_blank">
            <FaSquareFacebook className="w-8 sm:w-12 h-8 sm:h-12" />
            <span className="text-xs">Facebook</span>
          </a>
          <a href={data.whatsapp} className={styles.social} target="_blank">
            <FaSquareWhatsapp className="w-8 sm:w-12 h-8 sm:h-12" />
            <span className="text-xs">Whatsapp</span>
          </a>
          <a href={data.tiktok} className={styles.social} target="_blank">
            <AiFillTikTok className="w-8 sm:w-12 h-8 sm:h-12" />
            <span className="text-xs">Linkedin</span>
          </a>
        </div>
      </footer>
    </>
  );
};

export default Footer;

const styles = {
  social: "flex flex-col items-center justify-center mx-4",
};
