import React, { useEffect, useState } from "react";
import { LayoutProps } from "../interfaces";
import Navigation from "../components/nav";
import CookieConsent from "react-cookie-consent";
import { useTranslation } from "gatsby-plugin-react-i18next";
import SEO from "../components/seo";
import Footer from "../components/footer";
import images from "../images";
import { FaSquareFacebook, FaSquareWhatsapp } from "react-icons/fa6";
import { BiLogoInstagramAlt } from "react-icons/bi";
import { AiFillTikTok } from "react-icons/ai";

const Layout: React.FC<LayoutProps> = ({ children, data }) => {
  const { t } = useTranslation();
  const [cookiesAccepted, setCookiesAccepted] = useState(false);
  const [isPanelOpen, setIsPanelOpen] = useState(false);

  useEffect(() => {
    if (cookiesAccepted) {
      enableGoogleAnalytics();
    } else {
      disableGoogleAnalytics();
    }
  }, [cookiesAccepted]);

  const enableGoogleAnalytics = () => {
    if (typeof window !== "undefined") {
      const gtag = (window as any).gtag;
      if (gtag) {
        gtag("consent", "update", {
          analytics_storage: "granted",
        });
      }
    }
  };

  const disableGoogleAnalytics = () => {
    if (typeof window !== "undefined") {
      const gtag = (window as any).gtag;
      if (gtag) {
        gtag("consent", "update", {
          analytics_storage: "denied",
        });
      }
    }
  };

  const togglePanel = () => {
    setIsPanelOpen(!isPanelOpen);
  };

  return (
    <>
      {data && (
        <>
          <SEO data={data} />
          <div className={styles.wrapper}>
            {/* Header Section */}
            {/* <div className="bg-terciary fixed top-0 left-0 w-full z-20"> ajuste para remover fondo */}
            <div className="fixed top-0 left-0 w-full z-20">
              <div className={styles.header}>
                <div className={styles.logoContainer}>
                  <img
                    src={images.logo_white.src}
                    alt={images.logo_white.alt}
                  />
                </div>

                <div className={styles.navbar}>
                  <Navigation data={data} />
                </div>
              </div>
            </div>

            {/* Side Panel */}
            <div className="relative">
              <div
                className={`fixed w-[150px] sm:w-[300px] top-0 left-0 h-full bg-gray-900 text-white shadow-lg transform transition-transform duration-300 z-50 ${
                  isPanelOpen ? "translate-x-0" : "-translate-x-full"
                }`}
                // style={{ width: "300px" }}
              >
                <button
                  className="absolute top-4 right-4 text-white text-xl"
                  onClick={togglePanel}
                  aria-label="Close Panel"
                >
                  &times;
                </button>
                <div className="flex flex-col items-center justify-center h-full gap-6">
                  <a
                    href={data.instagram}
                    className={styles.social}
                    target="_blank"
                  >
                    <BiLogoInstagramAlt className="w-8 sm:w-12 h-8 sm:h-12" />
                    <span className="text-xs">Instagram</span>
                  </a>
                  <a
                    href={data.facebook}
                    className={styles.social}
                    target="_blank"
                  >
                    <FaSquareFacebook className="w-8 sm:w-12 h-8 sm:h-12" />
                    <span className="text-xs">Facebook</span>
                  </a>
                  <a
                    href={data.whatsapp}
                    className={styles.social}
                    target="_blank"
                  >
                    <FaSquareWhatsapp className="w-8 sm:w-12 h-8 sm:h-12" />
                    <span className="text-xs">Whatsapp</span>
                  </a>
                  <a
                    href={data.tiktok}
                    className={styles.social}
                    target="_blank"
                  >
                    <AiFillTikTok className="w-8 sm:w-12 h-8 sm:h-12" />
                    <span className="text-xs">Linkedin</span>
                  </a>
                </div>
              </div>

              {/* Content Overlay */}
              {isPanelOpen && (
                <div
                  className="fixed inset-0 bg-black bg-opacity-50 z-40"
                  onClick={togglePanel}
                />
              )}

              {/* Button to Open Panel */}
              <button
                className="fixed top-3/4 -rotate-90 -left-12 bg-dark text-white px-4 py-2 rounded-lg shadow-lg z-50 hover:bg-terciary transition-colors"
                onClick={togglePanel}
              >
                Contáctenos
              </button>
            </div>

            {/* Main Content */}
            <div
              className={`${
                styles.childrenContent
              } transition-transform duration-300 ${
                isPanelOpen ? "translate-x-36 sm:translate-x-72" : ""
              }`}
            >
              <main>{children}</main>
            </div>

            {/* Footer Section */}
            <div className="flex w-full" id={data?.menu[3]}>
              <Footer data={data} />
            </div>
            <a href={data.whatsapp} className={`${styles.whatsapp}`} target="_blank">
              <FaSquareWhatsapp className="w-12 sm:w-16 h-12 sm:h-16" />
              <span className="text-xs hidden text-wrap sm:block">{t("messageUs")}</span>
            </a>
          </div>

          {/* Cookie Consent */}
          <CookieConsent
            location="bottom"
            buttonText={t("granted")}
            declineButtonText={t("denied")}
            enableDeclineButton
            cookieName="gatsby-google-analytics-consent"
            expires={150}
            onAccept={() => setCookiesAccepted(true)}
            onDecline={() => setCookiesAccepted(false)}
            overlay={true}
            buttonClasses="!border !px-6 !py-2 !rounded-full !bg-light"
            declineButtonClasses="!border !px-6 !py-2 !rounded-full !bg-dark"
            overlayClasses="!bg-dark/90"
            containerClasses="bg-dark border-2 border-light rounded-2xl gradient p-2 sm:p-4 poppins-regular"
            contentClasses="text-white"
          >
            {t("cookieConsent")}
          </CookieConsent>
        </>
      )}
    </>
  );
};

export default Layout;

const styles = {
  wrapper: "flex flex-col",
  header: "flex bg-dark my-3 mx-4 p-6 sm:p-2 rounded-2xl self-center",
  logoContainer: "flex h-16 mx-auto",
  navbar: "order-2 sm:order-1 flex",
  childrenContent: "text-light mt-[8.5rem] sm:mt-24",
  social: "flex flex-col items-center justify-center mx-4",
  whatsapp: "fixed table text-[#25D366] bottom-4 sm:bottom-8 right-[unset] sm:right-16 left-2 sm:[left]-unset z-40",
};
