import React from "react";
import { Helmet } from "react-helmet";
import { Company } from "../interfaces";

interface Props {
    data: Company;
}

const SEO:React.FC<Props> = ({ data }) => {
  const schemaMarkup = {
    "@context": "https://schema.org",
    "@type": "LocalBusiness",
    "name": data.companyName,
    "image": "https://scontent.fbjx2-1.fna.fbcdn.net/v/t39.30808-6/447998731_971051345024445_4525864127124073854_n.jpg?stp=cp6_dst-jpg_tt6&_nc_cat=108&ccb=1-7&_nc_sid=cc71e4&_nc_ohc=V7OcsJdYRx4Q7kNvgEtyxpS&_nc_zt=23&_nc_ht=scontent.fbjx2-1.fna&_nc_gid=AZD5rLucOVF_BA_fYGU_sYs&oh=00_AYAzUHj_U6dWvD_DYi8_WD-iPKZne-im12lImBwsS0Uqsw&oe=67554317",
    "address": {
      "@type": "PostalAddress",
      "streetAddress": data.address,
      "addressLocality": data.city,
      "addressRegion": data.province,
      "postalCode": data.postalCode,
      "addressCountry": "MX"
    },
    "geo": {
      "@type": "GeoCoordinates",
      "latitude": 20.69289,
      "longitude": -101.4119673
    },
    "url": data.url,
    "telephone": data.phone,
    "priceRange": "$$",
    "openingHours": "Mo-Fr 09:00-17:00"
  };

  return (
    <Helmet>
      <script type="application/ld+json">
        {JSON.stringify(schemaMarkup)}
      </script>
    </Helmet>
  );
};

export default SEO;
